
.sfc-button {
  display: inline-block;
  height: 24px;
  padding: 0 5px;
  border: 1px solid #666;
  border-color: #3b91ad;
  color: #3b91ad;
  border-radius: 4px;
}
.sfc-month-hint {
  margin-bottom: 10px;
  text-align: center;
  zoom: 1;
  height: auto;
}
.sfc-month-hint:before,
.sfc-month-hint:after {
  content: " ";
  display: table;
}
.sfc-month-hint:after {
  clear: both;
  visibility: hidden;
  font-size: 0;
  height: 0;
}
.sfc-month-hint .month-info {
  float: left;
}
.sfc-month-hint .operations {
  float: right;
}
.sfc-wrapper {
  width: 800px;
  margin: auto;
}
.sfc-wrapper .sfc-table-wrapper {
  position: relative;
  margin-top: -1px;
}
.sfc-wrapper .sfc-table-wrapper .sfc-events-table {
  position: absolute;
  top: 0;
  left: 0;
}
.sfc-wrapper .sfc-table {
  width: 100%;
  table-layout: fixed;
  border-spacing: 0;
  border-collapse: collapse;
}
.sfc-wrapper .sfc-table .sfc-table-bg td {
  border: 1px solid #666;
  height: 80px;
}
.sfc-wrapper .sfc-table-head {
  width: 100%;
  text-align: center;
}
.sfc-wrapper .sfc-table-head td {
  border: 1px solid #666;
}
.sfc-wrapper .sfc-date {
  text-align: right;
  font-size: 14px;
}
.sfc-wrapper .sfc-date .not-current-month {
  color: #aaa;
}
.sfc-wrapper .sfc-events .sfc-event-item {
  margin: 1px 2px;
  padding: 2px;
  border-radius: 2px;
  border: 1px solid #666;
  border-color: #3b91ad;
  background-color: #3b91ad;
  color: #fff;
  font-size: 12px;
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
}


